function size (factor) {
    const size = Math.min(
        window.innerHeight,
        window.innerWidth
    )
    return size * factor / 100
}

// eslint-disable-next-line promise/param-names
const sleep = ms => new Promise(r => setTimeout(r, ms))

export async function execute ({
    func, scope, objs, board, interrupt, walls, configs = {
        strokeWidth: () => size(0.5),
        strokeColor: 'lightblue',
        highlightStrokeColor: 'lightblue'
    }
}) {
    let x = 0.5; let y = 0.5
    const mascot = board.create('point', [x, y], {
        size: () => size(1.2),
        name: ''
    })

    objs.push([mascot])
    const spd = 100

    async function move (d, axis) {
        const xc = x
        const yc = y

        const a = board.create('point', [xc, yc], {
            size: () => size(0.55),
            name: ''
        })
        const b = board.create('point', [xc, yc], {
            size: () => size(0.55),
            name: ''
        })
        const segment = board.create('segment', [a, b], configs)

        const dx = axis === 'x' ? d : 0
        const dy = axis === 'y' ? d : 0

        objs.push(...[a, b, segment])
        for (let xs = 0; xs <= dx; xs += 0.5) {
            for (let ys = 0; ys <= dy; ys += 0.5) {
                if (interrupt()) {
                    return
                }

                const xt = xc + xs
                const yt = yc + ys

                if (xt % 1 < 0.1) {
                    if ([[xt, Math.floor(yt)], [xt, Math.ceil(yt)]] in walls) {
                        interrupt = function () {
                            return true
                        }
                    }
                }

                if (yt % 1 < 0.1) {
                    if ([[Math.floor(xt), yt], [Math.ceil(xt), yt]] in walls) {
                        interrupt = function () {
                            return true
                        }
                    }
                }

                b.moveTo([xt, yt], spd)
                mascot.moveTo([xt, yt], spd)

                // eslint-disable-next-line @typescript-eslint/no-empty-function
                await sleep(spd)
            }
        }

        x += dx
        y += dy
    }

    func(scope = {
        ...scope,
        RIGHT: async function (d) {
            if (interrupt()) { return }
            await move(d, 'x')
        },
        UP: async function (d) {
            if (interrupt()) { return }
            await move(-d, 'y')
        },
        LEFT: async function (d) {
            if (interrupt()) { return }
            await move(-d, 'x')
        },
        DOWN: async function (d) {
            if (interrupt()) { return }
            await move(d, 'y')
        },
        blocked: async function (d) {
            return false
        }
    })
    return objs
}
