<template>
    <div id="editor"></div>
    <div id="field"></div>
</template>

<script>
import * as monaco from 'monaco-editor'
import * as JXG from 'jsxgraph'

import { lex } from '@/lang/lexer'
import { parse } from '@/lang/parser'
import { execute } from '@/lang/core'

monaco.languages.register({
    id: 'custom'
})

monaco.languages.setMonarchTokensProvider('custom', {
    keywords: [
        'IFBLOCK', 'ENDIF',
        'REPEAT', 'ENDREPEAT',
        'PROCEDURE', 'ENDPROC',
        'SET', 'CALL',
        'RIGHT', 'LEFT', 'UP', 'DOWN'
    ],
    tokenizer: {
        root: [
            [/[A-Z_$][\w$]*/, {
                cases: {
                    '@keywords': 'keyword',
                    '@default': 'identifier'
                }
            }],
            [/\d+/, 'number']
        ]
    }
})

let board

export default {
    name: 'app',
    async mounted () {
        function size (factor) {
            const size = Math.min(
                window.innerHeight,
                window.innerWidth
            )
            return size * factor / 100
        }

        const elem = document.querySelector('#editor')
        const editor = monaco.editor.create(elem, {
            value: '\n',
            language: 'custom',
            theme: 'vs-dark',
            fontSize: `${size(2.5)}px`,

            scrollbar: {
                verticalHasArrows: true,
                horizontalHasArrows: true
            }
        })

        board = JXG.JSXGraph.initBoard('field', {
            boundingbox: [-0.5, -0.5, 22, 22],
            axis: true,
            // keepAspectRatio: true,
            showCopyright: false,
            defaultAxes: {
                x: {
                    strokeColor: 'white',
                    highlightStrokeColor: 'white',
                    ticks: {
                        insertTicks: false,
                        ticksDistance: 1,
                        minorHeight: 0,
                        strokeColor: 'white',
                        label: {
                            strokeColor: 'white',
                            highlightStrokeColor: 'white',
                            offset: [0, -6],
                            anchorX: 'middle',
                            anchorY: 'top',
                            fontSize: () => size(2)
                        }
                    }
                },
                y: {
                    strokeColor: 'white',
                    highlightStrokeColor: 'white',
                    ticks: {
                        insertTicks: false,
                        ticksDistance: 1,
                        minorHeight: 0,
                        strokeColor: 'white',
                        label: {
                            strokeColor: 'white',
                            highlightStrokeColor: 'white',
                            offset: [6, 0],
                            anchorX: 'left',
                            anchorY: 'middle',
                            fontSize: () => size(2)
                        }
                    }
                }
            },
            drag: {
                enabled: false
            },
            pan: {
                enabled: true,
                needShift: true
            },
            zoom: {
                min: 1,
                max: 2
            }
        })

        addEventListener('resize', function () {
            editor.updateOptions({
                fontSize: `${size(2.5)}px`
            })
            editor.layout()
        })

        const objs = []
        let id = 0
        const interrupts = [false]
        const walls = {}

        addEventListener('keydown', async function (event) {
            if (event.keyCode !== 115) return
            for (const obj of objs) {
                board.removeObject(obj)
            }

            interrupts[id] = true
            interrupts.push(false)
            id++

            const cid = id
            console.log(interrupts)

            await execute({
                ...parse(lex(editor.getValue())),
                interrupt: function () {
                    return interrupts[cid]
                },
                board,
                objs,
                walls
            })
        })

        board.on('down', function (event, i) {
            const cPos = board.getCoordsTopLeftCorner(event, i)
            const absPos = JXG.getPosition(event, i)

            const dx = absPos[0] - cPos[0]
            const dy = absPos[1] - cPos[1]

            let coords = new JXG.Coords(JXG.COORDS_BY_SCREEN, [dx, dy], board)
            const [_, x, y] = coords.usrCoords

            const [lx, mx, ux] = [Math.floor(x), Math.round(x), Math.ceil(x)]
            const [ly, my, uy] = [Math.floor(y), Math.round(y), Math.ceil(y)]

            if (Math.abs(mx - x) < 0.1) {
                coords = [[mx, ly], [mx, uy]]
            } else if (Math.abs(my - y) < 0.1) {
                coords = [[lx, my], [ux, my]]
            }

            if (coords instanceof JXG.Coords) {
                return
            }

            if (coords in walls) {
                board.removeObject(walls[coords])
                delete walls[coords]
            } else {
                walls[coords] = board.create('segment', coords, {
                    strokeColor: 'red',
                    highlightStrokeColor: 'red'
                })
            }
        })
        editor.focus()
    }
}
</script>

<style>
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

#app {
    height: 100%;
    width: 100%;

    display: flex;
}

#field {
    background-color: rgb(30, 30, 30);
}

@media (min-height: 100vw) {
    #app {
        flex-direction: column;
    }
    #editor, #field {
        height: 50vh;
        width: 100vw;
    }
}

@media (min-width: 100vh) {
    #app {
        flex-direction: row;
    }
    #editor, #field {
        height: 100vh;
        width: 50vw;
    }
}

</style>
